// src/js/render-functions.js
import SimpleLightbox from 'simplelightbox';
import 'simplelightbox/dist/simple-lightbox.min.css';

const galleryEl = document.querySelector('.gallery');
const loaderEl = document.querySelector('.loader');      // <span class="loader">
const loadMoreBtn = document.querySelector('.load-more');

const lightbox = new SimpleLightbox('.gallery a', {
  captionsData: 'alt',
  captionDelay: 250,
});

/**
 * Добавляет разметку галереи и обновляет lightbox
 */
export function createGallery(images) {
  const markup = images
    .map(
      ({
        largeImageURL,
        webformatURL,
        tags,
        likes,
        views,
        comments,
        downloads,
      }) => `
      <li class="gallery-item">
        <a class="gallery-link" href="${largeImageURL}">
          <img
            class="gallery-image"
            src="${webformatURL}"
            alt="${tags}"
            loading="lazy"
          />
        </a>
        <div class="gallery-info">
          <p class="gallery-info-item"><span>Likes</span> ${likes}</p>
          <p class="gallery-info-item"><span>Views</span> ${views}</p>
          <p class="gallery-info-item"><span>Comments</span> ${comments}</p>
          <p class="gallery-info-item"><span>Downloads</span> ${downloads}</p>
        </div>
      </li>`
    )
    .join('');

  galleryEl.insertAdjacentHTML('beforeend', markup);
  lightbox.refresh();
}

export function clearGallery() {
  galleryEl.innerHTML = '';
}

/**
 * Показать текст "Loading..." вместо кнопки
 */
export function showLoader() {
  loadMoreBtn.classList.add('is-hidden');     // скрываем кнопку
  loaderEl.classList.remove('is-hidden');     // показываем текст
}

/**
 * Скрыть текст "Loading..." и вернуть кнопку
 */
export function hideLoader() {
  loaderEl.classList.add('is-hidden');        // прячем текст
  loadMoreBtn.classList.remove('is-hidden');  // показываем кнопку
}

/**
 * Полностью скрыть кнопку (когда дошли до конца коллекции)
 */
export function hideLoadMoreButton() {
  loadMoreBtn.classList.add('is-hidden');
}

/**
 * Показать кнопку (если есть что ещё догружать)
 */
export function showLoadMoreButton() {
  loadMoreBtn.classList.remove('is-hidden');
}
